.chosen {
  margin: auto;
  width: 100%;
  padding: 20px 10%;
  border-style: none;
  color: #5c7cbe;
  text-align: center;
  max-height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.letseat {
  font-size: 35px;
  font-family: "Patrick Hand", cursive;
  letter-spacing: 0.3rem;
  margin: 15px auto;
}

.nameinfo {
  color: black;
  font-size: 25px;
  margin-top: 10px;
}

.addressinfo {
  color: black;
  margin-top: 20px;
}

.distanceinfo {
  margin-top: 20px;
}

.chosenbuttons {
  width: 100%;
  margin: 30px auto;
  display: inline-block;
}

.chooseagain {
  margin: 30px auto;
  width: 280px;
  height: 60px;
  color: #5c7cbe;
  background-color: white;
  border: 1px solid #d1d1d1;
  border-radius: 10px;
  letter-spacing: 0.3rem;
  font-size: 30px;
  font-family: "Patrick Hand", cursive;
}

.chooseagain:active {
  transform: scale(0.98);
  box-shadow: 1px 1px 5px 1px #888888;
}

.nochosen {
  margin: auto;
  width: 80%;
  padding-top: 20px;
  border-style: none;
  color: #5c7cbe;
  text-align: center;
}

.nochosentext {
  font-size: 30px;
  margin: 30px auto;
  font-family: "Patrick Hand", cursive;
  letter-spacing: 0.2rem;
}

.placephotodiv {
  width: 100%;
  height: 300px;
}
.placephoto {
  height: 100%;
  width: auto;
  margin: auto;
}

.golink {
  margin: auto;
  width: 100px;
  height: 60px;
  font-size: 30px;
  color: white;
  background-color: #5c7cbe;
  border-style: none;
  border-radius: 10px;
  letter-spacing: 0.3rem;
  text-decoration: none;
  padding: 7px;
  display: block;
  font-family: "Patrick Hand", cursive;
}

.golink:visited {
  text-decoration: none;
}

.golink:active {
  transform: scale(0.98);
  box-shadow: 1px 1px 5px 1px #5c7cbe;
}
