.filter {
  margin: auto;
  width: 100%;
  padding: 20px 10%;
  border-style: none;
  color: #5c7cbe;
  max-height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

#input {
  width: 100%;
  height: 30px;
}

#keywordinput {
  width: 100%;
  height: 30px;
  margin-bottom: 10px;
}

#distance {
  width: 100%;
  display: flex;
  margin-bottom: 10px;
  margin-top: 40px;
}

#prices {
  width: 100%;
  display: flex;
  margin: auto;
  margin-bottom: 40px;
}

#cuisine {
  width: 100%;
  display: flex;
  padding-bottom: 80px;
}

#chewsbuttondiv {
  width: 100%;
  display: flex;
}

#chewsbutton {
  width: 313px;
  margin: auto;
  margin-bottom: 30px;
  border-style: none;
  border-radius: 5px;
  padding: 0;
}

#buildplatebutton {
  width: 300px;
  margin: auto;
  margin-bottom: 30px;
  border-style: none;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 1px #c7c7c7;
  padding: 0;
}

#chewsbutton:active {
  transform: scale(0.98);
  box-shadow: 1px 1px 5px 1px #888888;
}

#buildplatebutton:active {
  transform: scale(0.98);
  box-shadow: 1px 1px 5px 1px #888888;
}

#mcbutton {
  width: 100%;
}

#keyworddiv {
  margin-bottom: 40px;
}

#note {
  font-size: 12px;
}